.soc-round {
  list-style: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  * {
    box-sizing: border-box;
  }

  li {
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 100%;
    margin: 10px;
    transition: all 0.4s ease-in-out;

    a {
      display: block;
      width: 100%;
      height: 100%;
      padding: 10px;
      border: none;
      border-style: none;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &.facebook {
      background-color: #3b5998;
      &:hover {
        background-color: lighten(#3b5998, 10%);
      }

      a {
        color: #fff;
      }
    }


    &.twitter {
      background-color: #55acee;
      &:hover {
        background-color: lighten(#55acee, 10%);
      }

      a {
        color: #fff;
      }
    }
    &.linkedin {
      background-color: #007bb5;
      &:hover {
        background-color: lighten(#007bb5, 10%);
      }

      a {
        color: #fff;
      }
    }
    &.google-plus {
      background-color: #dd4b39;
      &:hover {
        background-color: lighten(#dd4b39, 10%);
      }

      a {
        color: #fff;
      }
    }
    &.pinterest {
      background-color: #cb2027;
      &:hover {
        background-color: lighten(#cb2027, 10%);
      }

      a {
        color: #fff;
      }
    }
    &.behance {
      background-color: #1769ff;
      &:hover {
        background-color: lighten(#1769ff, 10%);
      }

      a {
        color: #fff;
      }
    }
    &.dribbble {
      background-color: #ea4c89;
      &:hover {
        background-color: lighten(#ea4c89, 10%);
      }

      a {
        color: #fff;
      }
    }
    &.youtube {
      background-color: #bb0000;
      &:hover {
        background-color: lighten(#bb0000, 10%);
      }

      a {
        color: #fff;
      }
    }
    &.e-mail {
      background-color: #2c2c2c;
      &:hover {
        background-color: lighten(#2c2c2c, 10%);
      }

      a {
        color: #fff;
      }
    }
  }
}

